.login {
  width: 27em;
  height: 40em;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 130px;
  position: relative;
  z-index: 99;
}
.login .container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color:  #ffffff;
  box-shadow: 0px 0px 12px 2px rgba(34, 30, 24, 0.2);
  border-radius: 4px;
  position: relative;
  z-index: 99;
  width: 100%;
  height: 100%;
  z-index: 99;
  padding: 17px 10px;
}
.login .right-side {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 90%;
  background-color: #b8b8b8;
  width: 100%;
  position: absolute;
  right: -34%;
  border-radius: 6px;
  z-index: 1;
  transition: all 400ms ease-in-out;
  cursor: pointer;
  box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.281);
}
.login .right-side.right {
  right: -40%;
  align-items: flex-end;
}
.login .right-side.right:hover {
  right: -45%;
}
.login .right-side.left {
  right: 40%;
  align-items: flex-start;
}
.login .right-side.left:hover {
  right: 45%;
}
.login .right-side .text {
  font-size: 21px;
  font-weight: 500;
  color: #fff;
  margin-right: 3em;
  margin-left: 3em;
}

.submit-button {
  font-size: 21px;
  padding: 5px 20px;
  border: 0;
  background-color: #db7f34;
  border-radius: 50px;
  color: #fff;
  border-radius: 3px;
  transition: all 250ms ease-in-out;
  cursor: pointer;
}
.submit-button:hover {
  background-color: #c87323;
}
.submit-button:focus {
  outline: none;
}

